import React from 'react';
import './index.css'
import Topbar from './topbar/index.js'
import api from './api'


import DialogLoader from './helpers/dialogLoader'
import DialogResponse from './helpers/dialogResponse'
import Mosaik from './mosaik'
import Sketch from './sketch'





function ListItemChanger(props) {
  return (<div className= "listItemChanger">{props.value}</div>)
}




export class Picto extends React.Component{
  constructor(props){
      super(props);
      this.state={      
        openUploader: true,
        img: null,
        imgUrl: null,
        multiImg: [],
        multiImgUrl: [],
        responseImg: null,
        loader: false,
        mobile: false,
        infoMultipic: false,
        infoMainpic: false,
        backend_data: null,
        connect: false,
        resCount: '0%',
        setOptions: 1,
        responseDialog: false,
         
      }
  }
  componentDidMount(){
    this.connection()
    window.addEventListener("resize", this.updateWindowDimensions());
  }


  updateWindowDimensions() {
    const mobil = window.innerWidth<=500
    document.documentElement.style.setProperty('--vh', `${window.innerHeight/100}px`);
    this.setState({ mobile: mobil},()=>console.log('mobil  ',mobil));
  }

  connection = async()=>{
    await api.pingConnect().then(res=>{
      if(res.data.connect){
        this.setState({connect: true})
      }
    })
  }


  


  removeResponsePic = ()=>{
    this.setState({
        openUploader: true,
        img: null,
        imgUrl: null,
        multiImg: [],
        multiImgUrl: [],
        responseImg: null,
        loader: false,
        infoMultipic: false,
        infoMainpic: false,
        backend_data: null,
        resCount: '0%',
        responseDialog: false,
        
    })
  }



 

  

  


  

  

  
      
    



  

  changer = ()=>{
    const optionsList = ['Mosaikbiler', 'Sketchbild']
    const buttonList = []
    optionsList.forEach((e,index)=>{
      const focus = this.state.setOptions === index+1
      buttonList.push(<button className='changerBtn'  style={{ background: focus ? "whitesmoke" : "black", color: focus ? "black" : "white",
      }}  onClick={()=>{this.setState({setOptions:index + 1})}}>{e}</button>)
    })


    const listItems = buttonList.map((item) =>
        <ListItemChanger value={item} key={buttonList.indexOf(item) } />);

    return listItems;
  }

  mainView = ()=>{
    switch(this.state.setOptions){
      case 1:
        return (
          <Mosaik 
            mobile={this.state.mobile} 
            connect={this.state.connect} 
            responseImg={(e)=>this.setState({responseImg:e, responseDialog: true})} 
            loadCount={(e)=>this.setState({resCount:e})}
            loader={(e)=>this.setState({loader:e})}
            
            />
        )
      case 2:
        return (
          <Sketch 
            mobile={this.state.mobile} 
            connect={this.state.connect} 
            responseImg={(e)=>this.setState({responseImg:e, responseDialog: true})} 
            loader={(e)=>this.setState({loader:e})}
            />
        )
      



      default: return null
    }

  }





  render(){
      return(
        <div className='main'>
          <Topbar connect={this.state.connect}/>
          {this.mainView()}
          <DialogLoader open={this.state.loader} resCount={this.state.resCount} mobile={this.state.mobile} setOpt={this.state.setOptions}/>
          <DialogResponse open={this.state.responseDialog} responseImg={this.state.responseImg} mobile={this.state.mobile} setOpt={this.state.setOptions} close={()=>this.removeResponsePic()}/>
          <div className='changerdiv'>{this.changer()}</div>
        </div>
      ); 
  }
}

export default Picto;
