import React from 'react';
import './index.css';
import Dialog from '@material-ui/core/Dialog';
import BarLoader from "react-spinners/BarLoader";




         


export class DialogLoader extends React.Component{
    constructor(props){
        super(props);
        this.state={


        }
      
    }



    

    

    render(){
        console.log('count upload ', this.props.resCount)
        
        return(
            <Dialog 
            className={!this.props.mobile?'loadDialog':'loadDialog_mobile'}
            open={this.props.open===true}
            fullScreen={true}>
                 <div className='loaderScreen'>
                    {this.props.setOpt===1?<button className='sendBtn' ><span className='sendBtn_span'>{this.props.resCount}</span></button> :null}
                    <div className='loader'><BarLoader loading={true} size={170} color='black'/></div>      
                </div>
            </Dialog>
        ); 
    }
}

export default DialogLoader;



  