import React from 'react';
import './index.css'
import {MdCancel,MdInfoOutline} from 'react-icons/md' 
import ImgHandle from './../helpers/imgClass';
import MultiImageUpload from './../helpers/imgClassMulti'
import InfoDialog from './../helpers/dialogInfo'
import Creator from './../creator'





function ListItem(props) {
  return (<div className= "listItem">{props.value}</div>)
}


const infoTextMainpic = 'info'

const infoTextMultipic = 'info multi'



export class Mosaik extends React.Component{
  constructor(props){
      super(props);
      this.state={      
        openUploader: true,
        img: null,
        imgUrl: null,
        multiImg: [],
        multiImgUrl: [],
        loader: false,
        infoMultipic: false,
        infoMainpic: false,
         
      }
  }
  componentDidMount(){
  }


  updateWindowDimensions() {
  }

  componentWillUnmount(){
    console.log('unmount')
  }



  













  croppedImg = async (e)=>{ 
    const picName = this.state.userName
    let file = await fetch(e)
    .then(r => r.blob())
    .then(blobFile => new File([blobFile], picName, {type: "image/jpeg", name: 'file' }))
    console.log('los gehts......',e)

    this.setState({
        img:file,
        imgUrl:e,
    })
  }

  no_croppedMultiImg = async (srcArray)=>{ 
    for ( const e of srcArray){
      const picName = this.state.userName
    let file = await fetch(e)
      .then(r => r.blob())
      .then(blobFile => new File([blobFile], picName, {type: "image/jpeg", name: 'file' }))
   
    this.setState({
        multiImg:this.state.multiImg.concat(file),
        multiImgUrl:this.state.multiImgUrl.concat(e),
    })
    }
  }
    










  // multipics show
  //
  multiImagCubes = ()=>{
    const urlArray = this.state.multiImgUrl
    const imgList = []
    urlArray.forEach((e, index) => {
      if(index <= 60){
        imgList.push(
          <div className='image_cube_div'>
            <img className='image_cube' src={e} alt={'...'}></img>
            <button className='btn_cancel' onClick={()=>this.removeMultiPic(index)}><MdCancel size={20}/></button>
          </div>
        )
      }
      
    });
    const listItems = imgList.map((item) =>
        <ListItem value={item} key={imgList.indexOf(item) } />);

    return listItems;
  }

  removeMultiPic = (index)=>{
    const urlArray = this.state.multiImgUrl
    const imgArray = this.state.multiImg
    urlArray.splice(index, 1)
    imgArray.splice(index, 1)
    this.setState({multiImgUrl: urlArray, multiImg:imgArray})
  }

  

  infoDialogCallback = ()=>{
    this.setState({infoMainpic: false, infoMultipic:false})
  }


  mainFrameLeft = ()=>{
    if(this.state.img){
      return(
        <div className={!this.props.mobile?'left_side_picto':'top_side_picto'}>
        <div className='bez_window'></div>
          <div className={!this.props.mobile?'btn_1':'btn_1_mobile'}>
            <img className='image_1' src={this.state.imgUrl} alt={'...'}></img>
            <button className='btn_cancel' onClick={()=>{this.setState({img: null, imgUrl:null})}}><MdCancel size={20}/></button>
        </div>
        </div>
      )
    }else{
      return(
        <div className={!this.props.mobile?'left_side_picto':'top_side_picto'}>
          <InfoDialog mobile={this.state.mobile} open={this.state.infoMainpic} text={infoTextMainpic} closed={this.infoDialogCallback}/>
          <button className='btn_info' onClick={()=>{this.setState({infoMainpic: true})}}>
            <MdInfoOutline size={30}/>
          </button> 
          <div className='bez_window'>Vollbid:</div>
            <button className={!this.props.mobile?'btn_1':'btn_1_mobile'}>
            <ImgHandle img={this.croppedImg} open={this.state.openUploader}/>
            </button>
          </div>
      )
    }
  }

 

  

  

  mainFrameRight = ()=>{
    if(this.state.multiImgUrl.length>0){
      return(
        <div className={!this.props.mobile?'right_side_picto':'bottom_side_picto'}>
          {!this.props.mobile?
          <div className='bez_window'>
            <MultiImageUpload img={this.no_croppedMultiImg} open={this.state.openUploader}/></div>
          :
          <div className='bez_window_mobile'>
            <MultiImageUpload img={this.no_croppedMultiImg} open={this.state.openUploader}/>
          </div>
          }
          <div className={!this.props.mobile?'btn_2':'btn_2_mobile'}>
            {this.multiImagCubes()}
          </div>
        </div>
      )
    }else{
      return(
        <div className={!this.props.mobile?'right_side_picto':'bottom_side_picto'}>
          <InfoDialog mobile={this.props.mobile} open={this.state.infoMultipic} text={infoTextMultipic} closed={this.infoDialogCallback}/>
          <button className='btn_info' onClick={()=>{this.setState({infoMultipic:true})}}>
            <MdInfoOutline size={30}/>
          </button> 
          <div className='bez_window'>Mosaik-bilder:</div>
          <button className={!this.props.mobile?'btn_1':'btn_1_mobile'}>
            <MultiImageUpload img={this.no_croppedMultiImg} open={this.state.openUploader}/>
          </button>
        </div>
      )
    }
  }

  

  

  responseImg = (e)=>{
    this.props.responseImg(e)
    this.setState({img: null, imgUrl: null, multiImgUrl:[], multiImg:[]})
  }

  mainView = ()=>{
    const hidden = this.state.img !== null && this.state.multiImg.length > 0
    return(
      <div className={!this.props.mobile?'picto_1':'picto_1_mobile'}>
          {this.mainFrameLeft()}
            <Creator 
              hidden={hidden} 
              create={'mosaik'}
              mobile={this.props.mobile}
              img={this.state.img}
              multiImg={this.state.multiImg}
              responseImg={(e)=>this.responseImg(e)}
              loader={(e)=>this.props.loader(e)}
              loadCount={(e)=>this.props.loadCount(e)}
              />
          {this.mainFrameRight()}
        </div>
    )
  }
    







  render(){
    return(
        <div>{this.mainView()}</div>
    ); 
  }
}

export default Mosaik;
