import React from 'react';
import './index.css'
import {MdCancel,MdInfoOutline} from 'react-icons/md' 
import ImgHandle from './../helpers/imgClass';
import InfoDialog from './../helpers/dialogInfo'
import Creator from './../creator'



const infoTextMainpic = 'info'




export class Sketch extends React.Component{
  constructor(props){
      super(props);
      this.state={      
        openUploader: true,
        img: null,
        imgUrl: null,
        loader: false,
        mobile: false,
        infoMultipic: false,
        infoMainpic: false,
         
      }
  }
  componentDidMount(){
  
  }


  

  croppedImg = async (e)=>{ 
    const picName = this.state.userName
    let file = await fetch(e)
    .then(r => r.blob())
    .then(blobFile => new File([blobFile], picName, {type: "image/jpeg", name: 'file' }))
    console.log('los gehts......',e)

    this.setState({
        img:file,
        imgUrl:e,
    })
  }

  infoDialogCallback = ()=>{
    this.setState({infoMainpic: false, infoMultipic:false})
  }




  skeched = ()=>{
    return(
      <div className='middle_place'>
        <div className='sendBtn_div'>
          <button className='sendBtn' onClick={()=>this.sendFile()}><span className='sendBtn_span'>create</span></button>
        </div>
      </div>
    )
  }

  mainFrameCenter = ()=>{
    if(this.state.img){
      return(
        <div className={!this.props.mobile?'left_side_picto':'top_side_picto'}>
        <div className='bez_window'></div>
          <div className={!this.props.mobile?'btn_1':'btn_1_mobile'}>
            <img className='image_1' src={this.state.imgUrl} alt={'...'}></img>
            <button className='btn_cancel' onClick={()=>{this.setState({img: null, imgUrl:null})}}><MdCancel size={20}/></button>
        </div>
        </div>
      )
    }else{
      return(
        <div className={!this.props.mobile?'left_side_picto':'top_side_picto'}>
          <InfoDialog mobile={this.props.mobile} open={this.state.infoMainpic} text={infoTextMainpic} closed={this.infoDialogCallback}/>
          <button className='btn_info' onClick={()=>{this.setState({infoMainpic: true})}}>
            <MdInfoOutline size={30}/>
          </button> 
          <div className='bez_window'>Vollbid:</div>
            <button className={!this.props.mobile?'btn_1':'btn_1_mobile'}>
            <ImgHandle img={this.croppedImg} open={this.state.openUploader}/>
            </button>
          </div>
      )
    }
  }


    

  responseImg = (e)=>{
    this.props.responseImg(e)
    this.setState({img: null, imgUrl: null,})
  }




  render(){
    const hidden = this.state.img !== null
    return(
      <div className={!this.props.mobile?'picto_1':'picto_1_mobile'}>
        {this.mainFrameCenter()}
        <div className={!this.props.mobile?'right_side_picto':'bottom_side_picto'}>
          <Creator
            hidden={hidden} 
            create={'sketch'}
            mobile={false}
            responseImg={(e)=>this.responseImg(e)}
            loader={(e)=>this.props.loader(e)}
            img={this.state.img}
          />
        </div>
      </div>
    ); 
  }
}

export default Sketch;
