import React from 'react';
import './index.css'
import api from './../api'




export class Creator extends React.Component{
  constructor(props){
      super(props);
      this.state={      
        openUploader: true,
        img: null,
        imgUrl: null,
        multiImg: [],
        multiImgUrl: [],
        responseImg: null,
        loader: false,
        mobile: false,
        infoMultipic: false,
        infoMainpic: false,
        backend_data: null,
        connect: false,
        resCount: '0%',
        setOptions: 1
         
      }
  }
  componentDidMount(){
  }


 

  













  getPrecent = (length, count )=>{
    const prec = count / length * 100
    var intvalue = Math.trunc( prec );
    this.props.loadCount(intvalue.toString()) 
  }

  sendMultiFile = async(backend_data)=>{
    const multiPic = this.props.multiImg
    console.log('progressbar____')
    for( const [index, e]of multiPic.entries()){
      await api.insertForm_detail(backend_data.id, e).then(res=>{
        
        const length = multiPic.length
        this.getPrecent(length, index+1)
        
      })
    }
    await api.makeAPhoto(backend_data.id).then(res=>{
      console.log('finish    ',res.data)
      const img = res.data.img
      this.props.responseImg(img)
      this.props.loader(false)
    })
    
    
  }






  sendFile = async()=> {
    switch(this.props.create){
      case 'mosaik':
        const formData = new FormData()
        const mainPic = this.props.img
        formData.append('mainpic', mainPic)
        this.props.loader(true)
        await api.insertForm(formData).then(res=>{
      
          this.sendMultiFile(res.data)
      
        })
        break;
      case 'sketch':
        const formData2 = new FormData()
        const sketchPic = this.props.img
        formData2.append('mainpic', sketchPic)
        this.props.loader(true)
        await api.insertForm_sketch(formData2).then(res=>{
          const img = res.data.img
          this.props.responseImg(img)
      
      
        })
        break;
      default: return null
    }
    

  }

  

  


  creatorView = ()=>{
    if(this.props.mobile){
      return(
        <div className='send_btn_mobile_div'>
          <button className='sendBtn_mobile' onClick={()=>this.sendFile()}><span className='sendBtn_span'>create</span></button>
        </div>
      )
    }
    return(
      <div className='middle_place'>
        <div className='sendBtn_div'>
          <button className='sendBtn' onClick={()=>this.sendFile()}><span className='sendBtn_span'>create</span></button>
        </div>
      </div>
    )
  }


 

  



  
    


  



  render(){
    return(
        <div>
          {this.props.hidden?this.creatorView():null}
        </div>
    ); 
  }
}

export default Creator;
