import React from 'react';
import './index.css';
import Dialog from '@material-ui/core/Dialog';
import { TiArrowBackOutline } from "react-icons/ti";
import { saveAs } from 'file-saver';



         


export class DialogResponse extends React.Component{
    constructor(props){
        super(props);
        this.state={


        }
      
    }

    dialogClose = ()=>{
        this.props.close()
    }


    downloadFile = ()=>{
        const url = this.props.responseImg
        saveAs(url, 'photomosaik.jpg')
      }



    

    

    render(){
        return(
            <Dialog 
            className={!this.props.mobile?'loadDialog':'loadDialog_mobile'}
            open={this.props.open===true}
            fullScreen={true}>
                 <div className='dialog_res_div'>
                    <button className='btn_back_dialog' onClick={()=>{this.dialogClose()}}><TiArrowBackOutline size={50}/></button>
                    <div className='dialog_res_div_2'>
                        <button className='dialog_res_btn' onClick={()=>this.downloadFile()}>
                            <img className={!this.props.mobile?'dialog_res_img':'dialog_res_img_mobile'} src={this.props.responseImg} alt={'...'}></img>
                        </button>
                    </div>
                </div>
            </Dialog>
        ); 
    }
}

export default DialogResponse;



  